var convertData = function(data){
    var res = [];
    for(var i = 0;i < data.length;i ++){
        var geoCoord = geoCoordMap[data[i].name];
        if(geoCoord){
            res.push({
                name:data[i].name,
                value: geoCoord.concat(Number(data[i].value))
            });            
        }
        
    }
    return res;
}

